.topnav{
    background-color: #3f3f3f;
    overflow: hidden;
}

ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

li a{
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/*hover over li to be color #111 except for the active link*/
li a:hover{
    background-color: #111;
}

/*color for active link*/
.active{
    background-color: #12234e;
}
